









































































































































































































































































































































































































































































































































































































































































































































.VisitorAppointmentBox {
  .VisitorAppointment {
    width: 100%;
    height: 66.6vh;
    overflow: scroll;
    .VisitorAppointmentItem {
      width: 97vw;
      height: 66.6vh;
      margin: 0 auto;
      padding-bottom: 10vw;
      .appHomeTitBox {
        // height: 6.55vw;
        // line-height: 6.55vw;
        margin: 2vw 0 0 1vw;
        font-size: 4vw;
        // padding-top: 1vw;
        display: flex;
        justify-content: space-between;
        .titleName {
          font-size: 5vw;
          font-weight: bold;
        }
        .titleGreen {
          width: 23%;
          height: 6vw;
          line-height: 6vw;
          text-align: center;
          color: white;
          background-color: #4ecea3;
        }
        .appointment {
          width: 20vw;
          text-align: center;
          border-radius: 0.3vw;
          margin-right: 1vw;
          color: #ffffff;
          background-color: #20cfa4;
        }
      }
      .appHomeContent {
        margin-top: 2.6vw;
        width: calc(100% - 7.6vw);
        margin: 2.6vw 3.8vw 0;
        display: flex;
        flex-wrap: wrap;
        .contentItem {
          width: 100%;
          display: flex;
          margin-bottom: 2.42vw;
          position: relative;
          .RemainingNum {
            margin-right: 0.9vw;
            padding-left: 1.8vw;
            // font-size: 3.2vw;
            font-weight: bold;
            text-align-last: justify;
            color: rgba(107, 107, 107, 0.651);
            position: absolute;
            top: 22vw;
            left: 17vw;
          }
          .itemLabel {
            margin-right: 0.9vw;
            padding-left: 1.8vw;
            font-size: 3.8vw;
            font-weight: bold;
            color: #008c8a;
            text-align-last: justify;
          }
          .redSpot {
            position: relative;
            &::after {
              content: "*";
              color: red;
            }
          }
          .itemValue {
            flex: 1;
            line-height: 4.8vw;
            background: #f5f8fd;
            font-size: 3.2vw;
            font-weight: 400;
            text-align: left;
            color: #868786;
            padding-left: 1vw;
            position: relative;
            .AddDate {
              line-height: 21px;
              &::after {
                display: block;
                content: "";
                border-width: 1vw;
                border-style: solid;
                border-color: #00eab3 transparent transparent transparent;
                position: absolute;
                right: 3%;
                top: calc(50% - 0.75vw);
              }
            }
            input {
              width: 100%;
              border: none;
              background: #f5f8fd;
              padding: 0.5vw 0;
            }
            .btnBox {
              width: calc(100% - 12vw);
              padding: 0 6vw;
              height: 9.25vw;
              line-height: 9.25vw;
              margin-top: 2vw;
              display: flex;
              justify-content: space-between;
              font-size: 0;
              .optBtn {
                flex: 1;
                height: 9.25vw;
                line-height: 9.25vw;
                background: #f9f9f9;
                text-align: center;
                color: #535353;
                border-radius: 1vw;
                font-size: 4.44vw;
              }
              .timeOptBtn {
                width: 25vw;
                max-width: 25vw;
                &:hover {
                  background: rgba(0, 110, 159, 0.3);
                }
              }
              .timeOptBtn.active {
                background: rgba(0, 110, 159, 0.3);
              }
            }
            select {
              position: absolute;
              top: 0;
              width: 100%;
              border: none;
              z-index: 10;
              background: #f5f8fd;
            }
            .block {
              width: 100%;
              .AddDate {
                width: 100%;
                &::after {
                  display: block;
                  content: "";
                  border-width: 1vw;
                  border-style: solid;
                  border-color: #00eab3 transparent transparent transparent;
                  position: absolute;
                  right: 3%;
                  top: calc(50% - 0.75vw);
                }
              }
            }
          }
          .input2 {
            width: 100%;
            max-width: 77vw;
            display: flex;
            .AddDate {
              width: 40%;
            }
            input {
              margin-top: -0.5vw;
            }
          }
          .w77 {
            max-width: 77vw;
          }
          .valueBox {
            padding: 1vw;
            height: 20vw;
            overflow-y: scroll;
            max-width: 77vw;
            &::-webkit-scrollbar {
              display: none;
            }
          }
        }
      }
      .MandatoryBox {
        margin: 3vw 0 0 0;
        font-size: 3.6vw;
        align-content: center;
        .MandatoryBoxItem {
          display: flex;
          input {
            margin: 0.6vw 3vw 0 2vw;
          }
          div {
            color: #cacaca;
            span {
              color: #0379b0;
              border-bottom: 0.5vw #5a9aff solid;
            }
          }
        }
        .color8 {
          font-size: 3.3vw;
          color: #868786;
          list-style: none;
          margin: 2vw 0 0 1.8vw;
        }
      }
      .SubmitBtn {
        text-align: center;
        height: 6vh;
        line-height: 6vh;
        width: 90vw;
        font-size: 4vw;
        margin: 4vw auto 0;
        background-color: #15cfa4;
        color: white;
      }
      .mma {
        margin-top: 10vw;
      }
      .ItemTop {
        width: 100%;
        height: 6.2vw;
        display: flex;
        flex-direction: row-reverse;
        div {
          width: 23%;
          text-align: center;
          border: 0.5vw solid #008466;
          color: #008466;
        }
      }
      .ItemContent {
        color: #868786;
        font-size: 2.48vh;
        font-weight: 400;
        ul {
          height: 7vw;
          line-height: 7vw;
          background-color: #f9fffe;
          margin: 2vw 0;
          border-radius: 1vw;
          list-style: none;
          display: flex;
          padding: 3vw 1vw;
          li {
            width: 30%;
            margin-left: 2vw;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
          li:nth-child(1) {
            width: 32%;
          }
          li:nth-child(2) {
            margin-left: 3vw;
          }
          li:nth-child(3) {
            // margin-left: 12vw;
            display: flex;
            align-items: center;

            div {
              margin-left: 1vw;
            }
          }
          .ICTime {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
      .mask {
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.404);
        z-index: 15;
        .MessageBox {
          width: 77vw;
          height: 25vh;
          background-color: white;
          margin: 30vh auto;
          .MessageTop {
            color: white;
            display: flex;
            justify-content: center;
            height: 6vh;
            line-height: 6vh;
            background-color: #15cfa4;
            position: relative;
            img {
              position: absolute;
              top: 2vw;
              right: 1vw;
              width: 6vw;
              height: 6vw;
            }
          }
          .MessageBom {
            margin: 7vw 0 0 10vw;
            font-weight: 900;
          }
        }
      }
    }
    .startTime {
      font-size: 4vw;
      margin-top: 4vw;
      padding-left: 6vw;
      .ksname {
        display: inline-block;
        vertical-align: middle;
        width: 68vw;
        height: 9.25vw;
        line-height: 9.25vw;
        background: #f9f9f9;
        text-align: center;
        color: #535353;
        border-radius: 1vw;
        font-size: 4vw;
      }
    }
  }
}
